/*Media query Responsive Start*/



@media screen and (max-width: 600px) {
  h3{
    font: 24px roboto, sans-serif !important;
    font-weight: 800 !important;
  }
  
  h4{
    font: 14px roboto, sans-serif !important;
    font-weight: 600 !important;
    margin: 0 0 1rem 0;
  }
  

  #root{
    background: linear-gradient(to right, #667db6, #0082c8, #0082c8, #667db6);
    height:auto;
  }


  #card1,#card2,#card3{
    width: 90% !important;
    height : 80px !important;
    margin: 10px!important;
}

#card_amount{
  position: absolute;
  top : 12px;
  margin-bottom: 20px;
}

#card_title{
  position: absolute;
  bottom : 6px ;
}

#topBar{
  margin-top: 20px;
}
.filterdate{
 flex-direction: row;
}

#topBar,#row1,#row2,#container1,.main_div{ 
      display: block!important;
  }

  .main_div{justify-content: center;}
  .navbar{
      display: none!important;
  }

  .expense2{
    display: block !important;
    width: 100%;
  }

  .log_amount,#card_title{
      font: 12px roboto, sans-serif;
      font-weight: bold;
  }

  .inputs{
      width : 100%!important;
      height: auto;
  }


  #expense,#income{
      width: 100%;
      height: auto;
      margin: 10px auto!important;
  }

  .history{
      width: 100%;
      /* height: 77.53vh; */
      margin : auto!important;
  }

  #BarChartIncome,#row2,#BarChartExpense{
    margin: 10px auto !important;
    width : auto !important;
    height : auto !important; 
  }


}


/*Media query Responsive End*/




/* Form styles*/
#Amount, #Description{
  width: 100%;
}

#Amount:hover,#Description:hover {
  border: solid 1px #23C2EC;
  background-color: #23c0ec1c;
}


.catagory {
  display: flex;
}


#expense .catagory input, #income .catagory input{
	display: none;
}

#expense .catagory label,#income .catagory label{
	position: relative;
	color: #333;
  background: rgba( 255, 255, 255, 0.25 );
  box-shadow: 0 8px 32px 0 rgba( 31, 38, 135, 0.37 );
  backdrop-filter: blur( 13.0px );
  -webkit-backdrop-filter: blur( 13.0px );
  border-radius: 8px;
  border: 1px solid rgba( 255, 255, 255, 0.18 );

	font: 12px roboto, sans-serif;
  font-weight: 600;
	text-align: center;
  width : 88px;
	height: 32px;
	line-height: 32px;
  border: solid 1px #c4c4c4 ;
	display: block;
  padding: 0 8px 0 8px;
	cursor: pointer;
	-webkit-box-sizing: border-box;
	-moz-box-sizing: border-box;
	box-sizing: border-box;
  margin: 6px ;
}


#expense .catagory label:hover,#income .catagory label:hover{
 border: solid 2px #c4c4c4;
 border-width: 2px 2px 0px 2px;
}




#submit_expense{	cursor: pointer; 
  width: 100% !important ;
border: solid 1px #23C2EC;}

#submit_expense:hover{
border: solid 1px #23C2EC;
background-color: #23c0ec3f;}

